import React, { Component } from 'react';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import Drawer from '@material-ui/core/Drawer';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import ListIcon from '@material-ui/icons/List';
import AddIcon from '@material-ui/icons/Add';
import Paper from '@material-ui/core/Paper';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import Textfield from '@material-ui/core/Textfield';
import produce from 'immer';
import {Route, Link} from 'react-router-dom';
import axios from 'axios';

class App extends Component {
    state = {
        drawer_open: false,
        notes: [
            {'id': 234356, 'name': 'Note1', 'content': 'yoyoyo'},
            {'id': 732622, 'name': 'Note2', 'content': 'zozozo'},
        ],
        new_note: "",
        add_note_open: false
    };

    functions() {
        axios.get('https://chris-byu-todo-app.herokuapp.com/lists/').then(res => {
            console.log(res);
            this.setState({
                lists: res.data
            })
        })
    }

    onDeleteNote = (note_id) => {
        const note_index = this.state.notes.findIndex(note => note.id === note_id);
        this.props.history.push('/notes/');
        this.setState({
            notes: produce(this.state.notes, (draft) => {
                draft.splice(note_index, 1)
            })
        });
    };

    handleAddNote = (note_id) => {
        if (this.state.new_note !== "") {
            const new_id = Math.floor(Math.random()*90000) + 10000;
            this.setState({
                notes: produce(this.state.notes, (draft => {draft.push({'id': new_id, 'name': this.state.new_note, 'content': ''})})),
                new_note: "",
                add_note_open: false
            });
        }
    };


    render() {
        return (
            <div style={{flexGrow: 1}}>
                {/* The AppBar */}
                <AppBar position="static">
                    <Toolbar>
                        <IconButton style={{marginLeft: -12, marginRight: 20}} color="inherit"
                                    onClick={() => this.setState({drawer_open: true})}>
                            <MenuIcon/>
                        </IconButton>
                        <Route path={'/tasks/list/:id'} render={(routeProps) => {
                            return (
                                <Typography variant="title" color="inherit"
                                            style={{flexGrow: 1}}>{this.state.lists.length > 0 && this.state.lists.find(list => list.id === parseInt(routeProps.match.params.id, 10)).name}</Typography>
                            )
                        }}
                        />
                        <Route exact path={'/tasks/'}
                               render={(routeProps) => <Typography variant="title" color="inherit"
                                                                   style={{flexGrow: 1}}>To Do List</Typography>}/>
                        <Button color="inherit" component={Link} to={'/'}>Sign Out</Button>
                    </Toolbar>
                </AppBar>

                {/* The Drawer */}
                <Drawer open={this.state.drawer_open} onClose={() => this.setState({drawer_open: false})}>
                    <div>
                        <ListItem>
                            <ListItemIcon>
                                <AddIcon/>
                            </ListItemIcon>
                            <ListItemText style={{fontWeight: "bold"}} primary="All Notes"/>
                        </ListItem>
                        {this.state.notes.map(note => {
                            return (
                                <ListItem key={note.id} button onClick={() => this.setState({drawer_open: false})}
                                          component={Link} to={'/notes/note/' + note.id}>
                                    <ListItemIcon>
                                        <ListIcon/>
                                    </ListItemIcon>
                                    <ListItemText primary={note.name}/>
                                </ListItem>
                            )})}
                        <ListItem button onClick={() => this.setState({add_note_open: true})}>
                            <ListItemIcon>
                                <AddIcon/>
                            </ListItemIcon>
                            <ListItemText primary="Create New Note"/>
                        </ListItem>
                    </div>
                </Drawer>

                {/* The Note */}
                <Route path={'/notes/note/:note_id'} render={routeProps => {
                    const note = this.state.notes.length > 0 ? this.state.notes.find(x => x.id === parseInt(routeProps.match.params.note_id, 10)) : {name: "", content: ""};
                    const note_id = parseInt(this.props.match.params.note_id, 10)
                    return (
                    <div style={{display: 'flex', justifyContent: 'center'}}>
                        <Paper style={{maxWidth: '900px', width: '100%', marginTop: '40px'}}>
                            <div>
                                <div style={{display: 'flex', padding: '24px 24px'}}>
                                    <Typography variant="headline" style={{flexGrow: 1}}>
                                        {this.state.notes.find(note => note.id === note_id).name}
                                    </Typography>
                                    <Button color="secondary" variant="raised" size="small" onClick={() => this.onDeleteNote()}>
                                        Delete List
                                    </Button>
                                </div>
                                <Textfield style={{marginBottom: 40}} placeholder={"Note Name"} fullWidth
                                           value={this.state.notes.find(note => note.id === note_id).name}/>
                                <Textfield style={{marginBottom: 40}} placeholder={"Note Content"} fullWidth
                                           value={this.state.notes.find(note => note.id === note_id).content}/>
                            </div>
                        </Paper>
                    </div>
                    )
                }}/>

                {/*The Add Note Dialog*/}
                <Dialog open={this.state.add_note_open} onClose={() => this.setState({add_note_open: false})}>
                    <DialogTitle>Create a new note</DialogTitle>
                    <DialogContent>
                        <ListItem>
                            <ListItemText primary={<TextField placeholder="What's your note's name?" style={{width: '100%'}}
                                                              value={this.state.new_note}
                                                              onChange={(e) => this.setState({new_note: e.target.value})}/>}/>
                            <ListItemIcon>
                                <IconButton onClick={this.handleAddNote}>
                                    <AddIcon/>
                                </IconButton>
                            </ListItemIcon>
                        </ListItem>
                    </DialogContent>
                </Dialog>
            </div>
        )
    }
}


